<template>
  <div className="login-form">
    <h2>Logowanie RFID</h2>
    <input v-model="username" placeholder="Nazwa uÅ¼ytkownika" type="text">
    <button @click="loginWithRFID">Zaloguj</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: ''
    };
  },
  methods: {
    loginWithRFID() {
      axios.post('/login', {username: this.username})
          .then(response => {
            if (response.data.status === 'success') {
              alert('Zalogowano pomyÅ›lnie: ' + response.data.user);
              localStorage.setItem('isAuthenticated', 'true');
              window.dispatchEvent(new Event('login'))
              this.$router.push('/');  // Przekierowanie na stronÄ™ gÅ‚Ã³wnÄ… po zalogowaniu
            } else {
              alert(response.data.message);
            }
          })
          .catch(error => {
            console.error('BÅ‚Ä…d logowania RFID:', error);
            alert('BÅ‚Ä…d logowania: ' + error.message);
          });
    }
  }
}
</script>
